import { Home, Login, Portfolio, Profile, Projects } from "./pages";
import { Contact } from "./pages/Contact";

interface RoutesOptions {
    path: string;
    Component(props?: any): JSX.Element;
};

export const AppRoutes: RoutesOptions[] = [
    {
        path: '/',
        Component: Home
    },
    {
        path: '/home',
        Component: Home
    },
    {
        path: '/login',
        Component: Login
    },
    {
        path: '/about',
        Component: Profile
    },
    {
        path: '/portfolio',
        Component: Portfolio
    },
    {
        path: '/contact',
        Component: Contact
    },
    {
        path: '/projects',
        Component: Projects
    }
];