const experience: number = (() => {
    const startDate = new Date("2019-08-19");
    const difference = new Date().getTime() - new Date(startDate).getTime();
    const years = new Date(difference).getFullYear() - 1970;
    const months = new Date(difference).getMonth();
    return years + Number(`0.${months}`);
})();

