import styles from './index.module.scss';
import React from 'react';
import { Button, Card, CardContent, CardMedia, Grid, Link, Stack, Typography } from '@mui/material';
import { PageContainer, PrimaryText } from '../../components';
import { aboutMe, PersonalProfile, SocialProfile } from '../../Data';

interface HomeProps {
    children?: React.ReactNode;
};

export const Home = (props: HomeProps): JSX.Element => {
    return (
        <PageContainer>
            <Card className={styles.home} sx={{ overflowY: 'auto', height: '100%', width: '100%', display: 'flex' }}>
                <CardContent>
                    <Grid container spacing={2}>
                        <Grid item xs={5}>
                            <CardMedia
                                component="img"
                                image={PersonalProfile.image}
                                alt="profile"
                                sx={{ width: "100%", height: "100%", borderRadius: "10px", boxShadow: '0 0 25px rgb(17 17 17)' }}
                            />
                        </Grid>
                        <Grid item xs={7} sx={{ textAlign: 'center' }}>
                            <Stack spacing={4}>
                                <Typography>Get To Know Me</Typography>
                                <Typography variant="h2" sx={{ textAlign: 'center', color: '#ffff', lineHeight: "100%", fontWeight: "bold" }}>{PersonalProfile.name}</Typography>
                                <PrimaryText variant="h5" sx={{ textTransform: 'uppercase', fontWeight: 'bold', lineHeight: "200%" }}>{PersonalProfile.designation}</PrimaryText>
                                <div>{SocialProfile.map(social => <Link key={social.link} href={social.link} target={'_blank'}> {<social.icon />}  </Link>)}</div>
                                <Typography><div style={{textAlign: 'justify'}} dangerouslySetInnerHTML={{__html: aboutMe}}/></Typography>
                                <div>
                                <Link href='/#/contact'>
                                <Button variant="contained" sx={{ marginRight: '1rem' }}>Hire Me</Button>
                                    </Link>
                                    
                                    <Link href='/#/about'>
                                        <Button variant="contained">About Me</Button>
                                    </Link>
                                </div>
                            </Stack>
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
        </PageContainer >
    );
};