import { Alert, AlertTitle } from '@mui/material';
import React from 'react';
import { Routes, Route, HashRouter } from "react-router-dom";
import { AppRoutes } from './App.routes';
import { SideBar } from './components';

function App() {
  const { innerWidth } = window;
  const [isAlert] = React.useState(innerWidth < 1000 ? true : false);

  return (
    <>
      <HashRouter>
        {
          isAlert
            ?
            <Alert severity="error" variant="filled" sx={{ margin: '1rem', marginTop: '70%' }}>
              <AlertTitle>Error</AlertTitle>
              Please try to open in <strong>windows</strong> / <strong>desktop</strong> mode
              <p>It's not accessible in android / ios devices</p>
            </Alert>
            :
            <>
              <SideBar />
              <Routes>
                {AppRoutes.map(router => <Route key={router.path} path={router.path} element={<router.Component />} />)}
              </Routes>
            </>
        }
      </HashRouter>
    </>
  );
}

export default App;
