import styles from './index.module.scss';
import { Box, Card, CardContent, Stack, Typography } from '@mui/material'
import React from 'react'
import { PageContainer, ProjectCard } from '../../components'
import { ProjectHistory } from '../../Data';

const stackSpacing: number = 2;

export const Projects = () => {
    return (
        <PageContainer>
            <Card className={styles.projects} sx={{ overflowY: 'auto', height: '100%', width: '100%', display: 'flex' }}>
                <CardContent>
                    <Stack spacing={stackSpacing} sx={{ textAlign: 'center' }}>
                        <Typography>Work I Have Done In My Employment</Typography>
                        <Typography variant="h2" sx={{ color: '#ffff', fontWeight: "bold" }}>
                            My <span className="primaryText">Projects</span>
                        </Typography>
                        <Box sx={{
                            display: 'flex',
                            flexWrap: 'wrap',
                            maxWidth: '100%',
                            borderRadius: 1,
                            justifyContent: "space-between"
                        }}>
                            {ProjectHistory.map(project => <ProjectCard projectDescription={project} />)}
                        </Box>
                    </Stack>
                </CardContent>
            </Card>
        </PageContainer>
    )
}
