import styles from './index.module.scss';
import React from 'react';
import { Alert, AlertProps, Box, Button, Card, CardContent, CircularProgress, Link, List, ListItem, ListItemAvatar, ListItemText, Snackbar, Stack, Typography } from '@mui/material';
import { PageContainer } from './../../components';
import { AccountCircle, AlternateEmail, Call, LocationOn } from '@mui/icons-material';
import { PersonalProfile, SocialProfile, SystemConfig } from '../../Data';
import { sendEmail } from '../../Utility';

const stackSpacing: number = 3;

export const Contact = () => {

    const initialForm = {
        myName: PersonalProfile.name.split(" ")[0],
        name: "",
        email: "",
        subject: "",
        message: ""
    };

    const [formData, setFormData] = React.useState({ ...initialForm });

    const [loading, setLoading] = React.useState<boolean>(false);
    const [toaster, setToaster] = React.useState<{ show: boolean, message: string, type?: AlertProps["severity"] }>({
        show: false,
        message: ""
    });

    const sendMailEvent = async (event: React.FormEvent<HTMLFormElement>) => {
        setLoading(true);
        event.preventDefault();
        const resp = await sendEmail(formData);
        if (resp.status == 200) setToaster({ show: true, message: SystemConfig.EmailSuccessMessage, type: "success" });
        else setToaster({ show: true, message: SystemConfig.EmailErrorMessage, type: "error" });
        setFormData({ ...initialForm });
        setLoading(false);
    }

    const setValue = (e: React.ChangeEvent<HTMLInputElement>) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    return (
        <>
            <PageContainer>
                <Card className={styles.contact} sx={{ overflowY: 'auto', height: '100%', width: '100%', display: 'flex' }}>
                    <CardContent sx={{ width: '100%' }}>
                        <Stack spacing={stackSpacing}>
                            <Typography sx={{ textAlign: 'center' }}>Feel Free To Contact Me Anytime</Typography>
                            <Typography variant="h2" sx={{ color: '#ffff', fontWeight: "bold", textAlign: 'center' }}>
                                My <span className="primaryText">Contacts</span>
                            </Typography>
                            <Box>
                                <Typography sx={{ textAlign: 'left', fontSize: 'x-large', fontWeight: 'bold', color: 'white' }}>Contact Info</Typography>
                                <Typography>
                                    Always available for freelance work if the right project comes along,
                                    Feel free to contact me!
                                </Typography>

                                <List sx={{ display: 'flex', }}>
                                    <ListItem>
                                        <ListItemAvatar>
                                            <AccountCircle className={styles.iconStyle} />
                                        </ListItemAvatar>
                                        <ListItemText primary={
                                            <b>Name</b>
                                        } secondary={
                                            <span className={styles.primaryText}>{PersonalProfile.name}</span>
                                        } />
                                    </ListItem>

                                    <ListItem>
                                        <ListItemAvatar>
                                            <LocationOn className={styles.iconStyle} />
                                        </ListItemAvatar>
                                        <ListItemText primary={
                                            <b>Location</b>
                                        } secondary={
                                            <span className={styles.primaryText}>{PersonalProfile.address}</span>
                                        } />
                                    </ListItem>

                                    <ListItem>
                                        <ListItemAvatar>
                                            <AlternateEmail className={styles.iconStyle} />
                                        </ListItemAvatar>
                                        <ListItemText primary={
                                            <b>Email Me</b>
                                        } secondary={
                                            <a style={{ textDecoration: 'none' }} className={styles.primaryText} href={"mailto:".concat(PersonalProfile.emailF).replace(' ', '')}>
                                                {PersonalProfile.email}
                                            </a>
                                        } />
                                    </ListItem>

                                    <ListItem>
                                        <ListItemAvatar>
                                            <Call className={styles.iconStyle} />
                                        </ListItemAvatar>
                                        <ListItemText primary={
                                            <b>Call Me</b>
                                        } secondary={
                                            <a style={{ textDecoration: 'none' }} className={styles.primaryText} href={"tel:".concat(PersonalProfile.phone).replace(' ', '')}>
                                                {PersonalProfile.phone}
                                            </a>
                                        } />
                                    </ListItem>
                                </List>

                                <div style={{ display: 'flex', justifyContent: 'center', padding: '1rem' }}>
                                    <span className={styles.divider}></span>
                                    <div style={{ display: 'flex', justifyContent: 'center' }}>
                                        {SocialProfile.map(social => <Link className={styles.socialMediaIcons} href={social.link} target={'_blank'}> {<social.icon />}  </Link>)}
                                    </div>
                                    <span className={styles.divider}></span>
                                </div>

                            </Box>
                            <form onSubmit={sendMailEvent}>
                                <Box className={styles.inputForm}>
                                    <Typography sx={{ textAlign: 'left', fontSize: 'x-large', fontWeight: 'bold', color: 'white' }}>Contact Me</Typography>
                                    <Box sx={{ display: 'flex', justifyContent: 'space-around' }}>
                                        <div>
                                            <input type="text" value={formData.name} name="name" placeholder='Name' onChange={setValue} required />
                                        </div>
                                        <div>
                                            <input type="email" value={formData.email} name="email" placeholder='Email' onChange={setValue} required />
                                        </div>
                                    </Box>
                                    <Box>
                                        <div>
                                            <input type="text" value={formData.subject} name="subject" placeholder='Subject' onChange={setValue} required />
                                        </div>
                                    </Box>
                                    <Box>
                                        <div>
                                            <textarea placeholder="Message" value={formData.message} name="message" rows={5} onChange={e => setFormData({ ...formData, [e.target.name]: e.target.value })} required></textarea>
                                        </div>
                                    </Box>
                                    <Box sx={{ display: 'flex', justifyContent: 'center' }}>
                                        <Button disabled={loading} sx={{ color: 'white' }} variant="outlined" type="submit">
                                            {loading ? <CircularProgress color='inherit' /> : 'Send Message'}
                                        </Button>
                                    </Box>
                                </Box>
                            </form>
                            <Box>
                                <iframe
                                    title="Location"
                                    src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d7124.1382376699175!2d75.8211179!3d26.7740666!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x396dc971b0061c29%3A0x671e9053eee4fbae!2sShri%20Yogendra%20Nagar%2C%20Sachivalay%20Nagar%2C%20Sitapura%2C%20Jaipur%2C%20Rajasthan%20302029!5e0!3m2!1sen!2sin!4v1652554973543!5m2!1sen!2sin"
                                    width="600"
                                    height="450"
                                    style={{ border: "0px", width: '100%', borderRadius: "10px" }}
                                    tabIndex={0}
                                    allowFullScreen
                                />
                            </Box>
                            <Typography sx={{ textAlign: "center" }}>Copyright 2023 All rigths reserved</Typography>
                        </Stack>
                    </CardContent>
                </Card>
            </PageContainer>
            <Snackbar
                open={toaster.show}
                autoHideDuration={5000}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right'
                }}
                onClose={() => setToaster({ show: false, message: "", type: undefined })}
            >
                <Alert variant='filled' severity={toaster.type} >{toaster.message}</Alert>
            </Snackbar>
        </>
    );
};