import styles from './index.module.scss';
import React from 'react';
import { Box, Stack, Typography } from '@mui/material';
import { PrimaryText } from '../PrimaryText';

interface ResumeCardProps {
    title?: string;
    subtitle?: string | React.ReactNode;
    children?: React.ReactNode;
};
export const ResumeCard = (props: ResumeCardProps) => {
    return (
        <Box className={styles.resumeCard}>
            <span className={styles['item-arrow']}></span>
            <Stack sx={{ marginTop: '-0.4rem' }} spacing={1}>
                <PrimaryText variant='h5'>{props.title}</PrimaryText>
                <Typography variant='h6'>{props.subtitle}</Typography>
                <div>
                    {props.children}
                </div>
            </Stack>
        </Box>
    )
};