import './index.scss';
import React from 'react';
import { Call, CasesSharp, CellWifiSharp, Home, Person, Article } from '@mui/icons-material';
import { useLocation } from 'react-router-dom';
import { Tooltip } from '@mui/material';

interface SideBarProps {
    children?: React.ReactNode;
};

export const SideBar = (props: SideBarProps) => {

    const history = useLocation();
    const [activeClass, setActiveClass] = React.useState<"home" | "about" | "projects" | "blog" | "contact" | string>("home");
    
    React.useEffect(() => {
        const path = history.pathname.replace(/\//g, '') as string;
        setActiveClass(path.length > 0 ? path : "home");
        console.log({path, activeClass});
    }, [history.pathname]);

    return (
        <>
            <div className="header-holder services-section text-center animate__animated animate__zoomIn">
                <div className="navigation">
                    <ul className="nav">
                        <li className={`list ${activeClass === "home" ? "active" : ""}`}>
                            <Tooltip title="Home" placement="left-start">
                                <a href="/#/home" className={`${activeClass === "home" ? "active" : ""}`} onClick={() => setActiveClass("home")}>
                                    <span className="icon">
                                        <Home />
                                    </span>
                                </a>
                            </Tooltip>
                        </li>
                        <li className={`list ${activeClass === "about" ? "active" : ""}`}>
                            <Tooltip title="About" placement="left-start">
                                <a href="/#/about" className={`${activeClass === "about" ? "active" : ""}`} onClick={() => setActiveClass("about")}>
                                    <span className="icon">
                                        <Person />
                                    </span>
                                </a>
                            </Tooltip>
                        </li>
                        <li className={`list ${activeClass === "projects" ? "active" : ""}`}>
                            <Tooltip title="Projects" placement="left-start">
                                <a href="/#/projects" className={`${activeClass === "projects" ? "active" : ""}`} onClick={() => setActiveClass("projects")}>
                                    <span className="icon">
                                        <Article />
                                    </span>
                                </a>
                            </Tooltip>
                        </li>
                        {/* <li className={`list ${activeClass === "blog" ? "active" : ""}`}>
                            <Tooltip title="Blog" placement="left-start">
                                <a href="/#/blog" className={`${activeClass === "blog" ? "active" : ""}`} onClick={() => setActiveClass("blog")}>
                                    <span className="icon">
                                        <CellWifiSharp />
                                    </span>
                                </a>
                            </Tooltip>
                        </li> */}
                        <li className={`list ${activeClass === "contact" ? "active" : ""}`}>
                            <Tooltip title="Contact" placement="left-start">
                                <a href="/#/contact" className={`${activeClass === "contact" ? "active" : ""}`} onClick={() => setActiveClass("contact")}>
                                    <span className="icon">
                                        <Call />
                                    </span>
                                </a>
                            </Tooltip>
                        </li>
                    </ul>
                </div>
            </div>
        </>
    )
};