import styles from './index.module.scss';
import React from 'react';
import { Card, CardContent, CardMedia, Grid, Link, List, ListItem, ListItemIcon, ListItemText, Stack, Typography } from '@mui/material';
import { PageContainer, ResumeCard, SkillCard } from '../../components';
import { aboutMe, AcademicProfile, CategoriesSkills, EmployementHistory, PersonalProfile, SocialProfile } from '../../Data';
import { formatDate } from '../../Utility';

const stackSpacing: number = 2;

interface ProfileProps {
    children?: React.ReactNode;
};

interface MyProfileOptions {
    [key: string]: string | React.ReactNode;
};

const myProfile: MyProfileOptions[] = [
    { "Name": PersonalProfile.name },
    { "E-Mail": <span className="primaryText"><a href={`mailto:${PersonalProfile.email}`}>{PersonalProfile.email}</a></span> },
    { "Phone": <span className="primaryText"><a href={`tel:${PersonalProfile.phone}`}>{PersonalProfile.phone}</a></span> },
    { "Address": PersonalProfile.address },
    { "Date of Birth": PersonalProfile.dob.toLocaleString('default', { month: 'long', year: "numeric", day: "2-digit" }) }
];

export const Profile = (props: ProfileProps): JSX.Element => {
    return (
        <PageContainer>
            <Card className={styles.about} sx={{ overflowY: 'auto', height: '100%', width: '100%', display: 'flex' }}>
                <CardContent>
                    <Grid container spacing={2}>
                        <Grid item xs={5}>
                            <CardMedia
                                component="img"
                                image={PersonalProfile.image}
                                alt="profile"
                                sx={{ width: "100%", height: "100%", borderRadius: "10px", boxShadow: '0 0 25px rgb(17 17 17)' }}
                            />
                        </Grid>
                        <Grid item xs={7} sx={{ textAlign: 'center' }}>
                            <Stack spacing={stackSpacing}>
                                <Typography>My Intro</Typography>
                                <Typography variant="h2" sx={{ color: '#ffff', fontWeight: "bold" }}>
                                    About <span className="primaryText">Me</span>
                                </Typography>
                                <div style={{ textAlign: 'left' }}>
                                    <Stack spacing={stackSpacing}>
                                        <Typography variant="h6" sx={{ color: '#ffff' }}>
                                            Who Am <span className="primaryText">I ?</span>
                                        </Typography>
                                        <Typography variant="h5" sx={{ color: '#ffff' }}>
                                            I'm {PersonalProfile.name}, A <span className="primaryText">{PersonalProfile.designation}</span>
                                        </Typography>
                                        <Typography><div style={{textAlign: 'justify'}} dangerouslySetInnerHTML={{ __html: aboutMe }} /></Typography>
                                        <div>
                                            <Typography variant="h4" sx={{ color: '#ffff', fontWeight: 'bold' }}>
                                                Personal <span className="primaryText">Informations</span>
                                            </Typography>
                                            <List sx={{ display: 'flex', height: '10rem', maxWidth: '100%', flexWrap: 'wrap', color: '#fff' }}>
                                                {
                                                    myProfile.map(profile =>
                                                        <ListItem>
                                                            <ListItemIcon className={styles['list-item-icon']}>{Object.keys(profile).join()}</ListItemIcon>
                                                            <ListItemText className={styles['list-item-text']}>{profile[Object.keys(profile).join()]}</ListItemText>
                                                        </ListItem>
                                                    )
                                                }
                                            </List>
                                        </div>
                                        <div className={styles.socialIcons}>
                                            {SocialProfile.map(social => <Link href={social.link} target={'_blank'}> {<social.icon />}  </Link>)}
                                        </div>
                                    </Stack>
                                </div>
                            </Stack>
                        </Grid>
                    </Grid>
                    <Stack spacing={stackSpacing}>
                        <Stack spacing={stackSpacing} sx={{ paddingTop: '4rem', textAlign: 'center' }}>
                            <Typography>Check Out My Resume</Typography>
                            <Typography variant="h2" sx={{ color: '#ffff', fontWeight: "bold" }}>
                                My <span className="primaryText">Resume</span>
                            </Typography>
                            <Stack spacing={stackSpacing}>
                                <Grid container spacing={2}>
                                    <Grid item xs={6}>
                                        <Stack spacing={stackSpacing}>
                                            <Typography variant="h4" sx={{ color: '#ffff', fontWeight: "bold" }}>Education</Typography>
                                            {AcademicProfile.map(academic => <ResumeCard title={`${academic.class} - ${academic.board}`} subtitle={academic.academy}>{`${academic.year} - ${academic.percentage}`}</ResumeCard>)}
                                        </Stack>
                                    </Grid>
                                    <Grid item xs={6}>
                                        <Stack spacing={stackSpacing}>
                                            <Typography variant="h4" sx={{ color: '#ffff', fontWeight: "bold" }}>Experience</Typography>
                                            {EmployementHistory.map(employment =>
                                                <ResumeCard
                                                    title={employment.designation}
                                                    subtitle={
                                                        <span>
                                                            {employment.company + " "}
                                                            <s style={{textDecoration: 'none', fontSize: 'small'}}>
                                                                {formatDate(employment.from)} - {employment.to instanceof Date ? formatDate(employment.to) : employment.to}
                                                            </s>
                                                        </span>
                                                    }>
                                                    <div dangerouslySetInnerHTML={{__html: employment.description.join("<br/>")}}/>
                                                </ResumeCard>
                                            )}
                                        </Stack>
                                    </Grid>
                                </Grid>
                            </Stack>
                        </Stack>
                        <Stack spacing={stackSpacing} sx={{ paddingTop: '4rem', textAlign: 'center' }}>
                            <Typography>My Level Of Knowledge In Some Tools</Typography>
                            <Typography variant="h2" sx={{ color: '#ffff', fontWeight: "bold" }}>
                                My <span className="primaryText">Skills</span>
                            </Typography>
                            <SkillCard skillDetails={CategoriesSkills} />
                        </Stack>
                    </Stack>
                </CardContent>
            </Card>
        </PageContainer >
    );
};