import styles from './index.module.scss';
import React from 'react'
import { keyToLabel, ProjectHistoryOptions } from '../../Data/Properties';
import { Box, Chip, List, ListItem, ListItemIcon, ListItemText, Stack, TextField, Typography } from '@mui/material';
import { CalendarMonthTwoTone } from '@mui/icons-material';
import DescriptionIcon from '@mui/icons-material/Description';
import DeveloperModeIcon from '@mui/icons-material/DeveloperMode';

interface ProjectCardProps {
    projectDescription: ProjectHistoryOptions;
};

export const ProjectCard = (props: ProjectCardProps) => {
    const date = `${props.projectDescription.from.toLocaleString('default', { month: 'short', year: "numeric" })} - ${props.projectDescription.to instanceof Date ? props.projectDescription.to.toLocaleString('default', { month: 'short', year: "numeric" }) : props.projectDescription.to}`;
    return (
        <Box className={styles.projectCard}>
            <Typography variant="h5">{props.projectDescription.name}</Typography>
            <Chip className={styles.chip} icon={<CalendarMonthTwoTone />} label={date} />
            <Stack spacing={2}>
                <Stack sx={{ paddingTop: '1rem' }}>
                    <Chip className={styles.chip} icon={<DescriptionIcon />} label="Description" />
                    <TextField
                        id="outlined-multiline-static"
                        className={styles.textArea}
                        multiline
                        defaultValue={` ${props.projectDescription.description.join('\n  ')}`}
                    />
                </Stack>
                <Stack>
                    <Chip className={styles.chip} icon={<DeveloperModeIcon />} label="Key Technologies" />
                    <List sx={{
                        display: 'flex',
                        flexWrap: 'wrap',
                        maxWidth: '100%',
                        borderRadius: 1,
                        justifyContent: "space-between"
                    }}>
                        {Object.keys(props.projectDescription.keyTechnologies).map(key =>
                            <ListItem sx={{ maxWidth: '50%' }}>
                                <ListItemIcon className={styles.itemKey}>{keyToLabel(key)}</ListItemIcon>
                                <ListItemText className={styles.itemLabel} sx={{ textAlign: 'right' }}>{props.projectDescription.keyTechnologies[key].join(', ')}</ListItemText>
                            </ListItem>
                        )}
                    </List>
                </Stack>
            </Stack>
        </Box>
    )
};