import styles from './index.module.scss';
import React from 'react';
import { Box, LinearProgress, LinearProgressProps, Stack, Typography } from '@mui/material';
import { CategoriesSkillsOptions, SkillOptions } from '../../Data/Properties';

interface SkillCardProps {
    title?: string;
    skillDetails?: CategoriesSkillsOptions;
};

export function LinearProgressWithLabel(props: LinearProgressProps & { value: number }) {
    return (
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Box sx={{ width: '100%', mr: 1 }}>
                <LinearProgress variant="determinate" {...props} />
            </Box>
            <Box sx={{ minWidth: 35, mr: '2rem' }}>
                <Typography className={styles.progress}>{`${Math.round(props.value)}%`}</Typography>
            </Box>
        </Box>
    );
}

export const SkillCard = (props: SkillCardProps) => {
    return (
        <Box className={styles.skillCard}>
            {props?.skillDetails ? Object.keys(props.skillDetails).map((key: string) => <div className={styles.category}>
                <Typography variant="h5" sx={{ textAlign: 'center', color: 'white' }}>{key}</Typography>
                {props?.skillDetails?.[key].map(skill => <div>
                    <Typography sx={{ textAlign: 'left', color: 'white' }}>{skill.name}</Typography>
                    <LinearProgressWithLabel value={skill.respondents ?? 100} />
                </div>)}
            </div>) : undefined}
        </Box>
    )
};